.container {
    display: flex; 
    flex-wrap: wrap;
}
.ingredient { 
    margin: 0 calc(1ex / 0.32) 0 0;
}
 a {

    margin: 0;
    color: #1976d2;
    -webkit-text-decoration: none;
    text-decoration: none;
}
a:hover {
    -webkit-text-decoration: underline;
    text-decoration: underline;
}
.image-container {
    width: 100%;
    height: 325px;

}
img {
    margin-left: auto;
    margin-right: auto;
}

section { 
    height: 100%
}
article {
    display: flex;
    height: 100%;
    justify-content: center;
}
article h1 {
    font-size: 2.5em;
    line-height: calc(1ex / 0.42);
    margin: 0;
}

article h2 {
    font-size: 2em;
    line-height: calc(1ex / 0.42);
    margin: calc(1ex / 0.42) 0;
}

article h3 {
    font-size: 1.75em;
    line-height: calc(1ex / 0.38);
    margin: calc(1ex / 0.38) 0;
}

article h4 {
    font-size: 1.5em;
    line-height: calc(1ex / 0.37);
    margin: calc(1ex / 0.37) 0;
}

article p {
    font-size: 1em;
    line-height: calc(1ex / 0.32);
    margin: calc(1ex / 0.32) 0;
}